                    <div class="grid grid-cols-2 gap-4">
                      <div class="flex items-center">
                        <font-awesome-icon 
                          :icon="['fas', packageData.limits?.customDomain ? 'check-circle' : 'times-circle']" 
                          :class="packageData.limits?.customDomain ? 'text-green-500' : 'text-red-500'" 
                          class="mr-2" />
