                      </div>

                      <div class="flex items-center">
                        <font-awesome-icon 
                          :icon="['fas', packageData.limits?.sslCertificate ? 'check-circle' : 'times-circle']" 
                          :class="packageData.limits?.sslCertificate ? 'text-green-500' : 'text-red-500'" 
                          class="mr-2" />
