            </section>

            <!-- Features Section -->
            <section aria-labelledby="features-title" v-if="packageData.features && packageData.features.length > 0">
              <div class="bg-white shadow sm:rounded-lg">
                <div class="px-4 py-5 sm:px-6">
                  <h2 id="features-title" class="text-lg font-medium leading-6 text-gray-900">
                    คุณสมบัติเพิ่มเติม
